	module.exports = {"data":{"markdownRemark":{"html":"<h2>Monte Carlo</h2>\n<p>The Monte Carlo is a class of simulation techniques that allow you to explore the solution space of a problem that has inputs that can take on multiple values. By running simulations with randomized inputs and model parameters, you can observe outcomes that result from inputs that may have other not been tested. The method is useful for solving problems that may be too difficult to solve analytically. It is not an exact method, but a heuristical one, typically using randomness and statistics to get a result. The algorithm terminates with an answer that is correct with probability.</p>\n<p>It is a computation process that uses random numbers to produce an outcome(s). Instead of having fixed inputs, probability distributions are assigned to some or all of the inputs. This will generate a probability distribution for the output after the simulation is run.</p>\n<p>For example, a Monte Carlo algorithm can be used to estimate the value of π. The amount of area within a quarter-circle of radius 1 depends on the value of π. The probability that a randomly-chosen point will lie in that quarter-circle depends on the area of the circle. If points are placed randomly in a square with sides of length 1, the percentage of points that fall within a quarter-circle of radius 1 will depend on the value of π. A Monte Carlo algorithm would randomly place points in the square and use the percentage of points falling inside of the circle to estimate the value of π.This is an effective way for making approximations.</p>\n<p>In modern communication systems, the quality of information exchange is determined by the presence of noise in the channel. The major source of noise - Additive White Gaussian Noise (AWGN) being random in nature can be characterized using the Monte Carlo algorithm in simulating a Communications System.</p>\n<h3>More Information:</h3>\n<ul>\n<li><a href=\"https://en.wikipedia.org/wiki/Monte_Carlo_method\">Wikipedia</a></li>\n<li><a href=\"http://mathworld.wolfram.com/MonteCarloMethod.html\">Wolfram MathWorld</a></li>\n<li><a href=\"http://blog.minitab.com/blog/understanding-statistics/monte-carlo-is-not-as-difficult-as-you-think\">Minitab article - Monte Carlo is not as difficult as you think</a></li>\n<li><a href=\"https://www.youtube.com/watch?v=Q2-FH36LuT0\">Monte Carlo Algorithm (4:41)</a></li>\n</ul>","fields":{"slug":"/machine-learning/monte-carlo/"},"frontmatter":{"title":"Monte Carlo"}}},"pathContext":{"slug":"/machine-learning/monte-carlo/"}}

